<template>
    <div class="content">
        <div class="header">
            <div class="backButton" @click="$router.push('/main')">< Back</div>
            <div class="title">
                <span @click="$router.push('/main/groupsetting')">{{$store.state.queryResult.name}}</span>
            </div>
        </div>
        <div class="msgBox">
            <div class="msgRow" v-for="msg in $store.state.queryResult.recMsg">
                <span class="senderName">{{msg.senderName}}</span>
                <span class="receiveTime">{{msg.time}}</span>
                <br>
                <span class="msgContent">{{msg.msg}}</span>
            </div>
        </div>
        <div class="inputBox">
            <div class="toolBar"></div>
            <textarea class="textarea" v-model="chatMsg" autofocus @keyup.enter="sendMsg(chatMsg)"></textarea>
            <div class="bottomBar">
                <div class="sendButton" @click="sendMsg(chatMsg)">发送</div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "GroupChat",
        data() {
            return {
                chatMsg: null,
            }
        },
        mounted() {
            // 初始化websocket
            if (typeof (WebSocket) === "undefined") {
                alert("您的浏览器不支持WebSocket!")
            } else {
                this.socket = new WebSocket('ws://127.0.0.1:8088/ws')
                this.socket.onopen = this.open
                this.socket.onerror = this.error
                this.socket.onmessage = this.getMessage
            }
        },
        methods: {
            // 定义websocket基础方法
            open() {
                console.log("socket连接成功");
                this.init();
            },
            error() {
                console.log("连接错误")
            },
            send(request) {
                this.socket.send(JSON.stringify(request));
            },

            getMessage(response) {
                let data = JSON.parse(response.data);
                console.log(data);
                if (data.action === 10) {
                    let date = new Date();
                    data.chatMsg.time = date.toLocaleString('chinese', {hour12: false});
                    this.$store.state.queryResult.recMsg.push(data.chatMsg)
                }
            },
            close() {
                console.log("socket已经关闭")
            },
            // 初始化
            init() {
                let initReq = {
                    "action": 1,
                    "chatMsg": {
                        "senderId": this.$store.state.currentUser.id
                    }
                };
                initReq.chatMsg.senderId = this.$store.state.currentUser.id;
                this.send(initReq)
            },
            // 发送消息
            sendMsg(msg) {
                msg = msg.replace('\n', '');
                if (!msg) {
                    this.chatMsg = null;
                    alert("发送内容不能为空，请重新输入")
                } else {
                    let chatReq = {
                        action: 10,
                        chatMsg: {
                            senderId: null,
                            groupId: null,
                            msg: null
                        }
                    };
                    chatReq.chatMsg.senderId = this.$store.state.currentUser.id;
                    chatReq.chatMsg.groupId = this.$store.state.queryResult.id;
                    chatReq.chatMsg.msg = msg.replace('\n', '');
                    this.send(chatReq);
                    // 把发送的消息写入对话框
                    // let date = new Date();
                    // chatReq.chatMsg.time = date.toLocaleString('chinese', {hour12: false});
                    // chatReq.chatMsg.senderName = this.$store.state.currentUser.name;
                    // this.$store.state.queryResult.recMsg.push(chatReq.chatMsg);
                    this.chatMsg = null
                }
            },
        }
    }

</script>

<style scoped>
    .content {
        padding: 25px;
    }

    .header {
        width: 100%;
        height: 60px;
        border-bottom: 1px solid #CCCCCC;
        text-align: right;
    }

    .title {
        height: 100%;
        font-size: 24px;
        font-weight: bolder;
        text-align: center;
        line-height: 70px;
    }

    .title > span {
        cursor: pointer;
    }

    .backButton {
        position: absolute;
        cursor: pointer;
        font-size: 20px;
        color: #888888;
    }


    .inputBox {
        position: absolute;
        bottom: 0;
        width: 650px;
        height: 300px;
        border-top: 1px solid #CCCCCC;
    }

    .toolBar {
        width: 100%;
        height: 30px;
    }

    .textarea {
        width: 644px;
        height: 220px;
        padding: 5px;
        outline: none;
        resize: none;
        border: none;
        background: #EEEEEE;
        font-size: 20px;
        font-family: Arial;
    }

    .bottomBar {
        text-align: right;
    }

    .bottomBar > span {
        color: #888888;
        font-size: 10px;
        margin-right: 20px;

    }

    .sendButton {
        display: inline-block;
        border: 1px solid #c1c1c1;
        text-decoration: none;
        background-color: #fff;
        color: #222;
        border-radius: 4px;
        padding: 3px 30px;
        font-size: 14px;
        cursor: pointer;
        user-select: none;
    }

    .sendButton:hover {
        background-color: #d8d8d8;
    }

    .senderName {
        margin-right: 20px;
    }

    .msgContent {
        word-wrap: break-word;
        word-break: break-all;
        padding-left: 10px;
    }

    .msgRow {
        padding: 5px;
        border-bottom: 1px solid #c1c1c1;
        line-height: 25px;
    }

    .receiveTime {
        float: right;
    }
</style>